<template>
    <div>我是点与点之间的关系网，贼牛逼
        <div class="print">{{GDS}}</div>
        <div class="print">{{GDSFLAG}}</div>
    </div>
</template>

<script>
    import {request} from "../../network/request";
    export default {
        name: "GraphInPM",
        data() {
            return {
                GDS: '',
                GDSFLAG: '',
                //result2: '',
            }
        },

        // window. 下属命名看来是全局命名，不能重名
        created() {
            window.GasRealtime = setInterval(() => {
                setTimeout(request({
                    url: "http://127.0.0.1:8000/gas_realtime_data/",
// method: "post",
                }).then(res=>{
                    this.GDS = res;
                }).catch(err=>{
// eslint-disable-next-line no-console
                    console.log(err);
                }), 0)
            }, 1000);

            window.GasFlag = setInterval(() => {
                setTimeout(request({
                    url: "http://127.0.0.1:8000/gas_flag_data/",
// method: "post",
                }).then(res=>{
                    this.GDSFLAG = res;
                }).catch(err=>{
// eslint-disable-next-line no-console
                    console.log(err);
                }), 0)
            }, 1000);

        },
        destroyed() {
            clearInterval(window.GasRealtime);
            clearInterval(window.GasFlag);
        }
    }
</script>

